.news-item {
  border-bottom: 1px solid #e1e1e1;
  padding: 10px 0;
}

.news-content {
  margin-left: 15px;
}

.news-link {
  text-decoration: none;
  color: black;
}

.news-title {
  font-family: "Noto Sans KR", Helvetica;
  font-size: 18px;
  font-weight: bold;
  color: #D2D4DC;
  margin-bottom: 8px;
  text-align: justify;
  line-height: 1.3;
}

.news-description {
  font-family: "Noto Sans KR", Helvetica;
  font-size: 14px;
  color: #555;
  text-align: justify;
  margin-bottom: 8px;
  line-height: 1.3;
  word-wrap: break-word;
}

.result-screen .gauge-labels {
