  width: 360px;
  justify-content: space-between; /*      */
  width: 380px;

}

.result-screen .gauge-label {
  font-family: 'Noto Sans KR', sans-serif; /*   */
  color: #3C3C3C; /*   */
  font-size: 14px; /*   */
  text-align: center; /*    */
  white-space: nowrap; /*    */
}

/*    */
.result-screen .gauge-label.left, 
.result-screen .gauge-label.left2 {
  font-weight: bold;
  color: #DF5341; /*   -   */
}

.result-screen .gauge-label.left2 {
  font-weight: bold;
  color: #782A2B; /*  -  / */
}

/*    */
.result-screen .gauge-label.middle {
  font-weight: bold;
  color: #ccc; /*  -   */
}

/*    */
.result-screen .gauge-label.right2, 
.result-screen .gauge-label.right {
  font-weight: bold;
  color: #1F3A82; /*  -   */
}

.result-screen .gauge-label.right {
  font-weight: bold; /*     */
  color: #3764F3; /*   -   */
}

